  border: 1px solid #ededed;
  -webkit-box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.5);
  position: relative;
  width: 300px;
  max-height: 470px;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 auto;
  padding: 0; }
