  :global(.pwt-datepicker > .pwt-date-time),
  :global(.pwt-datepicker > .pwt-datepicker-picker-section) {
    position: relative;
    display: block;
    overflow: hidden;
    width: 100%;
    float: left;
    height: 280px; }
  :global(.pwt-datepicker .pwt-date-navigator) {
    width: 100%;
    padding-right: 10px;
    padding-left: 10px;
    height: 48px;
    right: 0;
    left: 0; }
  :global(.pwt-datepicker .pwt-date-year-view),
  :global(.pwt-datepicker .pwt-date-month-view) {
    padding-top: 20px; }
    :global(.pwt-datepicker .pwt-date-year-view > div),
    :global(.pwt-datepicker .pwt-date-month-view > div) {
      height: 48px; }
  :global(.pwt-datepicker .pwt-date-view table) {
    width: 100%;
    border-spacing: 0; }
  :global(.pwt-datepicker .pwt-date-view tr),
  :global(.pwt-datepicker .pwt-date-view td) {
    height: 40px !important;
    padding: 0 !important; }
  :global(.pwt-datepicker .pwt-date-view th) {
    font-weight: normal;
    font-size: 1em;
    color: gray; }
  :global(.pwt-datepicker .pwt-date-view th),
  :global(.pwt-datepicker .pwt-date-view td) {
    padding: 0 !important;
    width: 38px !important; }
    :global(.pwt-datepicker .pwt-date-view th span.pwt-date-view-text),
    :global(.pwt-datepicker .pwt-date-view td span.pwt-date-view-text) {
      text-align: center;
      display: block;
      margin: auto;
      width: 36px;
      line-height: 42px; }
